body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    color: #333;

    header {
        background-color: #333;
        color: #fff;
        padding: 1rem;

        nav {
            ul {
                list-style: none;
                display: flex;

                li {
                    margin-right: 20px;

                    a {
                        color: #fff;
                        text-decoration: none;
                    }
                }
            }
        }
    }

    main {
        padding: 20px;

        .btn {
            background-color: #007BFF;
            color: white;
            padding: 10px;
            text-decoration: none;
            border-radius: 5px;
            display: inline-block;
        }

      
      .image-container {
            max-width: 100%;
            margin: 20px auto;
            border-radius: 8px;
            background-color: #f4f4f4;
            text-align: center; 

            img {
                width: 100%;
                max-width: 600px;
                height: auto;
                object-fit: cover;
                border-radius: 8px;
                display: block;
                margin: 0 auto;
            }

            figcaption {
                margin-top: 8px;
                font-size: 1.1rem;
                color: #666;
                max-width: 600px;
                text-align: center;
                margin: 0 auto;
            }
        }

      
    }

    footer {
        background-color: #333;
        color: white;
        text-align: center;
        padding: 1rem;
    }
}
